@import url("https://fonts.googleapis.com/css2?family=Overpass:wght@300;400;600;700;800&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@600&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --h5-20: Overpass;
  --font-open-sans: "Open Sans";
  --font-source-sans-pro: "Source Sans Pro";
  --font-roboto: Roboto;

  /* font sizes */
  --font-size-sm: 14px;
  --font-size-base: 16px;
  --font-size-17xl: 36px;
  --h5-20-size: 20px;
  --font-size-71xl: 90px;
  --font-size-lg: 18px;
  --font-size-45xl: 64px;

  /* Colors */
  --white: #fff;
  --text: #23283c;
  --color-gray-100: #23283b;
  --color-gray-200: rgba(255, 255, 255, 0.6);
  --color-ghostwhite-100: #f4f6fe;
  --referizzer-main: #4c70f0;
  --color-royalblue-100: rgba(19, 100, 246, 0.5);
  --b7b9c: #6b7b9c;
  --text-light: #6d7287;
  --c4257: #3c4257;
  --website-dark: #47536b;
  --color-aliceblue: #edf0f7;

  /* Gaps */
  --gap-50xl: 69px;
  --gap-554xl: 573px;
  --gap-58xl: 77px;
  --gap-25xl: 44px;
  --gap-6xl: 25px;
  --gap-xl: 20px;
  --gap-5xs: 8px;
  --gap-xs-2: 11.2px;
  --gap-smi: 13px;
  --gap-xs: 12px;
  --gap-base: 16px;
  --gap-61xl: 80px;
  --gap-mini-5: 14.5px;
  --gap-5xl: 24px;
  --gap-10xl: 29px;
  --gap-11xl: 30px;
  --gap-mini: 15px;

  /* Paddings */
  --padding-61xl: 80px;
  --padding-5xs: 8px;
  --padding-6xl: 25px;
  --padding-4xs: 9px;
  --padding-xs: 12px;
  --padding-10xs: 3px;
  --padding-base: 16px;
  --padding-3xs: 10px;
  --padding-xl: 20px;

  /* border radiuses */
  --br-5xs: 8px;
  --br-8xs-3: 4.3px;
  --br-7xs: 6px;
  --br-xs: 12px;
  --br-12xs: 1px;
  --br-10xs: 3px;
}
